/* ============ MIXINS ============= */
/* ============ END MIXINS ============= */
/**
 * Clear inner floats
 */
.clearfix::after {
  clear: both;
  content: "";
  display: table;
}

/**
 * Hide element while making it readable for screen readers
 * Shamelessly borrowed from HTML5Boilerplate:
 * https://github.com/h5bp/html5-boilerplate/blob/master/src/css/main.css#L119-L133
 */
.visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

.unstyle-list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.unstyle-list li {
  padding: 0;
  margin: 0;
}
.unstyle-list li:before {
  display: none;
}

.disable-scroll {
  overflow: hidden;
}

.open-modal {
  overflow: hidden;
  padding-right: 15px;
}

.display-none {
  display: none;
}

/**
 * Hide text while making it readable for screen readers
 * 1. Needed in WebKit-based browsers because of an implementation bug;
 *    See: https://code.google.com/p/chromium/issues/detail?id=457146
 */
.hide-text {
  overflow: hidden;
  padding: 0;
  /* 1 */
  text-indent: 101%;
  white-space: nowrap;
}

.cover {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.arrow {
  border: solid #234930;
  border-width: 0 2px 2px 0;
  display: inline-block;
  padding: 0.4rem;
}
.arrow--light {
  border-color: #234930;
}
.arrow.right {
  transform: rotate(-45deg);
}
.arrow.left {
  transform: rotate(135deg);
}
.arrow.up {
  transform: rotate(-135deg);
}
.arrow.down {
  transform: rotate(45deg);
}

.table-scroll {
  overflow-x: auto;
}

.arrow-img {
  width: 80%;
}

[data-aos=fade-custom] {
  opacity: 0;
  transition-property: opacity;
}
[data-aos=fade-custom].aos-animate {
  opacity: 1;
}

@media screen and (max-width: 768px) {
  [data-aos=fade-mobile] {
    opacity: 1 !important;
    transition-property: all !important;
    transition-delay: 0.5s;
    transition-timing-function: ease;
    transition-duration: 0.3s;
  }
  [data-aos=fade-mobile].aos-animate {
    background: #F7E900;
  }
}

@media screen and (max-width: 768px) {
  [data-aos=text-color-change] {
    opacity: 1 !important;
    transition-property: color !important;
    transition-delay: 0.6s;
    transition-timing-function: ease !important;
    transition-duration: 0.3s !important;
    color: rgba(255, 255, 255, 0.8) !important;
  }
  [data-aos=text-color-change].aos-animate {
    color: #234930 !important;
  }
}

