  transform: rotate(45deg) translate(10px, 5px);
}
.header__navigation.change .hamburger .line:nth-child(2) {
  opacity: 0;
}
.header__navigation.change .hamburger .line:nth-child(3) {
  transform: rotate(-45deg) translate(8px, -2px);
}
